.login {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: space-between;
}
.login .left_bg {
  flex: 1 1;
  background-image: url(../../static/media/bg2.3f3b1532.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-filter: blur(0.2px) grayscale(0.1);
          filter: blur(0.2px) grayscale(0.1);
  opacity: 0.8;
  display: flex;
  justify-content: center;
  align-items: center;
}
.login .left_bg h1 {
  font-size: 60px;
}
.login .login_container {
  padding: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 600px;
}
.login .login_container .login_form {
  background: rgba(255, 255, 255, 0.1);
  padding: 110px;
  box-sizing: border-box;
  border-radius: 1px 320px;
  box-shadow: 0px 1px 15px 5px #ccc;
  text-align: center;
}
.login .login_container .login_form .has-error .ant-form-explain,
.login .login_container .login_form .has-error .ant-form-split {
  color: #f5222d;
  text-align: left !important;
}
.login .login_container .login_form .ant-form-item label {
  left: -64px;
}
